import { Component, OnInit, ViewChild, Inject } from '@angular/core';
import { Router } from '@angular/router';
import { AuthService } from '@auth0/auth0-angular';
import { FormControl, UntypedFormGroup, Validators, ValidationErrors } from '@angular/forms';
import { OnlineStatusService, OnlineStatusType } from 'ngx-online-status';
import { ToastrService } from 'ngx-toastr';
import { generalValidator } from '../../data-models/validator.model';
import { Tel, TelInputComponent } from '../../tel-input/tel-input.component';
import { DOCUMENT } from '@angular/common';

@Component({
  selector: 'app-sign-up',
  templateUrl: './sign-up.component.html',
  styleUrls: ['./sign-up.component.css']
})
export class SignUpComponent implements OnInit {

  @ViewChild('telInput') telInput!: TelInputComponent;

  // show error message when field is invalid
  finError: string = '';

  // disable all buttons when authenticating
  sending: boolean = false;

  // regular expression for validation
  regex: string = '^([a-zA-Z]|[0-9]|\.|@)+$';

  signupForm: UntypedFormGroup = new UntypedFormGroup({
    firstName: new FormControl<string>('', [Validators.required]),
    lastName: new FormControl<string>('', [Validators.required]),
    phoneNum: new FormControl<Tel>(new Tel('', '', ''), [Validators.required, generalValidator()]),
    fin: new FormControl<string>('', [Validators.minLength(5), Validators.maxLength(8), Validators.pattern(this.regex)])
  });

  constructor(
    public auth: AuthService,
    private router: Router,
    private onlineStatusService: OnlineStatusService,
    private toastr: ToastrService,
    @Inject(DOCUMENT) public document: Document
  ) {

  }

  ngOnInit(): void {

    // license number validation
    this.signupForm.get('fin')?.valueChanges.subscribe(() => {
      let finErrors: ValidationErrors | null | undefined = this.signupForm.get('fin')?.errors;
      if (finErrors?.minlength || finErrors?.maxlength) {
        this.finError = '5 - 8 characters required';
      }
      else if (finErrors?.pattern) {
        this.finError = 'Invalid characters detected, numbers and letters only';
      }
      else {
        this.finError = '';
      }
    });

  }

  // for testing
  toHome(): void {
    this.router.navigate(['no-trip-home']);
  }

  // go back to the login screen
  toLogin(): void {
    if (this.onlineStatusService.getStatus() === OnlineStatusType.OFFLINE) {
      this.toastr.error("Cannot log out while offline", "Logout failed");
      return;
    }
    this.auth.logout({
      logoutParams: {
        returnTo: this.document.location.origin
      }
    });
  }

  // attempt to sign up for an account
  attemptSignUp(): void {

    this.sending = true;

    this.telInput.touched = true;
    this.telInput.stateChanges.next();
    this.signupForm.get('phoneNum')?.updateValueAndValidity();

    // check username and password validity
    if (this.signupForm.valid) {

      if (this.onlineStatusService.getStatus() === OnlineStatusType.OFFLINE) {
        this.toastr.error("Cannot sign up while offline", "Sign up failed");
        return;
      }

      // navigate to home screen
      this.router.navigate(['home']);

    }
    else {
      this.toastr.error("Missing credentials", "Sign up failed");
    }

    this.sending = false;
  }

}
