import { Component } from '@angular/core';
import { FormControl, UntypedFormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { AuthService, User } from '@auth0/auth0-angular';
import { ToastrService } from 'ngx-toastr';
import { LocalStorageService } from '../../services/local-storage.service';
import { UserData } from '../../data-models/user-models/user-data.model';
import { Trip } from '../../data-models/trip-models/trip.model';
import { PauseReport } from 'src/app/data-models/report-models/pause-report.model';
import { OnlineStatusService, OnlineStatusType } from 'ngx-online-status';

@Component({
  selector: 'app-pause-report',
  templateUrl: './pause-report.component.html',
  styleUrls: ['./pause-report.component.css']
})
export class PauseReportComponent {

  currTrip: Trip = new Trip();
  userDetails: UserData = new UserData('', '');
  pauseReport: PauseReport = new PauseReport();

  // report data
  pauseForm: UntypedFormGroup = new UntypedFormGroup({
    managementArea: new FormControl<string>('', [Validators.required]), // page 1
    comments: new FormControl<string>('') // page 2
  });
  pauseDate: Date = new Date(); // page 1

  // page to page
  numPages: number = 0;
  currPage: number = 0;
  spinnerProgress: number = 0;
  spinnerIncrement: number = 0;


  constructor(
    public auth: AuthService,
    private router: Router,
    private toastr: ToastrService,
    private onlineStatusService: OnlineStatusService,
    private localStorageService: LocalStorageService
  ) {

    if (this.onlineStatusService.getStatus() === OnlineStatusType.ONLINE) {
      this.auth.isAuthenticated$.subscribe((authd: boolean) => {
        if (authd) {
          this.auth.user$.subscribe((user: User | null | undefined) => {
            if (user) {
              this.userDetails = this.localStorageService.getUser(user);
              this.initialize();
              return;
            }
            else {
              this.toastr.error("Please authenticate again", "Cannot get user data");
              this.localStorageService.logout();
              return;
            }
          });
        }
        else {
          this.toastr.error("Please authenticate again", "Not authenticated");
          this.localStorageService.logout();
          return;
        }
      });
    } else { // user is offline
      let currID: string = this.localStorageService.getCurrID();
      this.userDetails = this.localStorageService.getUserByID(currID);
      this.initialize();

    }

  }

  initialize(): void {

    this.currTrip = this.localStorageService.validateCurrTrip(this.userDetails.currTrip);

    this.numPages = 2;
    this.spinnerIncrement = 100 / this.numPages;
    this.toNextPage();


  }


  // check current page before going to the next one
  toNextPage(): void {

    if (this.currPage <= this.numPages) {
      this.incrementPage();
    }
    else {
      this.sendToDFO();
    }

  }

  // forward one page
  incrementPage(): void {
    this.currPage++;
    this.spinnerProgress += this.spinnerIncrement;
  }

  // back one page
  toPreviousPage(): void {

    if (this.currPage === 1) {
      this.toHome();
      return;
    }

    this.currPage--;
    this.spinnerProgress -= this.spinnerIncrement;

  }

  // check if management area is invalid
  managementAreaInvalid(): boolean {
    return this.pauseForm.get('managementArea')?.errors?.required;
  }

  saveReportData() { 

    // save relevant information gathered in the form
    this.pauseReport.pauseDate = this.pauseDate;
    this.pauseReport.managementArea = this.pauseForm.get('managementArea')?.value;
    this.pauseReport.comments = this.pauseForm.get('comments')?.value;
    this.pauseReport.submissionDate = new Date(); // get current date/time

    // push the report to currTrip
    this.currTrip.pauseFishingReports.push(this.pauseReport);
    
    // save currTrip to local storage
     this.userDetails.currTrip = this.currTrip;
     this.localStorageService.saveData(this.userDetails.userID, this.userDetails); 
  }

  // send start fishing report to DFO
  sendToDFO(): void {
    if (this.pauseForm.valid) {
      this.saveReportData();
      this.router.navigate(['submitted-report'], { state: { name: 'Pause Fishing Report' } });
      return;
    }
    this.toastr.error("Not all required fields filled", "Invalid form");
  }

  // go back to the active trip home screen
  toHome(): void {
    this.router.navigate(['active-trip-home']);
  }

}
