export class Net {

    netType: string = ''; // will be either Alaska Twist, Multi Strand, or Comibination

    netName: string = ''; // will only be for the skipper's reference

    numStrands: string = ''; //only needed for Alaska Twist

    length: string = '';

    weedlineDepth: string = '';

    weedlineDepthUnits: string = '';

    hangRatio: string = '';

    meshSize: string = '';

    meshSizeUnits: string = '';

    numMeshes: string = '';

    isChecked: boolean = false; 

    constructor(type: string, name: string, length: string,
        depth: string, depthUnits: string, ratio: string, mesh: string, 
        meshUnits: string, numMeshes: string, strands: string = '', checked: boolean = false){

        this.netType = type;
        this.netName = name;
        this.length = length;
        this.weedlineDepth = depth;
        this.weedlineDepthUnits = depthUnits;
        this.hangRatio = ratio;
        this.meshSize = mesh;
        this.meshSizeUnits = meshUnits;
        this.numMeshes = numMeshes;
        this.numStrands = strands;
        this.isChecked = checked;
    }

    toString(): string {
        return this.netName + ' (' + this.netType + ')';
    }

    isValid(): boolean {

        if(this.netType === "Alaska Twist") { // requires numStrands to be set

            return this.netName != '' && this.numStrands != '' && this.length != '' &&
            this.weedlineDepth != '' && this.weedlineDepthUnits != '' && this.hangRatio != '' && 
            this.meshSize != '' && this.meshSizeUnits != '' && this.numMeshes != ''; 

        }else if(this.netType != '') { // does not require a value for numStrands

            return this.netName != '' && this.length != '' && this.weedlineDepth != '' && 
            this.weedlineDepthUnits != '' && this.hangRatio != '' && this.meshSize != '' && 
            this.meshSizeUnits != '' && this.numMeshes != ''; 

        }

        return false; 

      }

    // transform a data structure into net data
    static transformAnonymous(anon: any[]): Net[] {
        let list: Net[] = [];
        anon.forEach((Net: any) => {
            let item: Net = new Net(Net.netType, Net.netName, Net.numStrands, Net.length, Net.weedlineDepth,
            Net.weedlineDepthUnits, Net.hangRatio, Net.meshSize, Net.meshSizeUnits, Net.numMeshes, Net.isChecked);
            list.push(item);
        });
        return list;
    }

    
}
