import { Component } from '@angular/core';
import { Router } from '@angular/router';
import { AuthService, User } from '@auth0/auth0-angular';
import { UserData } from '../../data-models/user-models/user-data.model';
import { Trip } from 'src/app/data-models/trip-models/trip.model';
import { ToastrService } from 'ngx-toastr';
import { OnlineStatusService, OnlineStatusType } from 'ngx-online-status';
import { LocalStorageService } from '../../services/local-storage.service';

@Component({
  selector: 'app-submitted-report',
  templateUrl: './submitted-report.component.html',
  styleUrls: ['./submitted-report.component.css']
})
export class SubmittedReportComponent {

  //data preambles
  reportName: string = "";
  userDetails: UserData = new UserData('', '');
  currTrip: Trip = new Trip();

  constructor(
    public auth: AuthService,
    private router: Router,
    private toastr: ToastrService,
    private onlineStatusService: OnlineStatusService,
    private localStorageService: LocalStorageService
  ) {
    this.reportName = this.router.getCurrentNavigation()?.extras.state?.name;

     // get current fishing report information
    if (this.onlineStatusService.getStatus() === OnlineStatusType.OFFLINE) {
      let currID: string = this.localStorageService.getCurrID();
      this.userDetails = this.localStorageService.getUserByID(currID);
    }

    else {
      this.auth.isAuthenticated$.subscribe((authd: boolean) => {
        if (authd) {
          this.auth.user$.subscribe((user: User | null | undefined) => {
            if (user) {
              this.userDetails = this.localStorageService.getUser(user);
            }
            else {
              this.toastr.error("Please authenticate again", "Cannot get user data");
              this.localStorageService.logout();
              return;
            }
          });
        }
        else {
          this.toastr.error("Please authenticate again", "Not authenticated");
          this.localStorageService.logout();
          return;
        }
      });
    }
  }

  // go back to the home screen
  toHome(): void {
    this.router.navigate([this.userDetails.currHomepage]);
  }

}
