import { Component } from '@angular/core';
import { Router } from '@angular/router';
import { AuthService, User } from '@auth0/auth0-angular';
import { LocalStorageService } from '../../services/local-storage.service';
import { OnlineStatusService, OnlineStatusType } from 'ngx-online-status';
import { ToastrService } from 'ngx-toastr';
import { UserData } from '../../data-models/user-models/user-data.model';
import { Trip } from 'src/app/data-models/trip-models/trip.model';

@Component({
  selector: 'app-active-trip-home',
  templateUrl: './active-trip-home.component.html',
  styleUrls: ['./active-trip-home.component.css']
})
export class ActiveTripHomeComponent {

  //data preambles
  userDetails: UserData = new UserData('', '');
  currTrip: Trip = new Trip();

  constructor( // same constructor as original homepage
    public auth: AuthService,
    private router: Router,
    private toastr: ToastrService,
    private onlineStatusService: OnlineStatusService,
    private localStorageService: LocalStorageService
  ) {

    if (this.onlineStatusService.getStatus() === OnlineStatusType.OFFLINE) {
      let currID: string = this.localStorageService.getCurrID();
      this.userDetails = this.localStorageService.getUserByID(currID);
      this.initialize();
    }

    else {
      this.auth.isAuthenticated$.subscribe((authd: boolean) => {
        if (authd) {
          this.auth.user$.subscribe((user: User | null | undefined) => {
            if (user) {

              // fill trip, net, license area, net and species lists
              this.userDetails = this.localStorageService.getUser(user);
              this.initialize();

            }
            else {
              this.toastr.error("Please authenticate again", "Cannot get user data");
              this.localStorageService.logout();
              return;
            }
          });
        }
        else {
          this.toastr.error("Please authenticate again", "Not authenticated");
          this.localStorageService.logout();
          return;
        }
      });
    }

  }

  initialize(): void {
    // get current fishing report information
    this.currTrip = this.localStorageService.validateCurrTrip(this.userDetails.currTrip);
  }

  getGearName(): string {
    return this.currTrip.gear.name;
  }

  getDate(date: Date): Date {
    return new Date(date);
  }

  toCatchReport(gearType: string): void {
    switch(gearType){
      case 'Gill Net':
        this.router.navigate(['gill-net-report']);
        break;
      case 'Seine':
        this.router.navigate(['seine-report']);
        break;
      case 'Troll':
        this.router.navigate(['troll-report']);
        break;
    }
  }

  // go to end a fishing report
  toEndReport(): void {
    this.router.navigate(['end-report']);
  }

  // go to pause a fishing report  
  toPauseReport(): void {
    this.router.navigate(['pause-report']);
  }

  // go to cancel the current trip
  toCancelReport(): void {
    this.router.navigate(['cancel-report']);
  }

  // go to an offload report
  toOffloadReport(): void {
    this.router.navigate(['offload-report']);
  }

}
