import { Component } from '@angular/core';
import { Router } from '@angular/router';
import { UserData } from '../../data-models/user-models/user-data.model';
import { AuthService, User } from '@auth0/auth0-angular';
import { ToastrService } from 'ngx-toastr';
import { OnlineStatusService, OnlineStatusType } from 'ngx-online-status';
import { LocalStorageService } from '../../services/local-storage.service';
import { Trip } from '../../data-models/trip-models/trip.model';
import { CancelReport } from 'src/app/data-models/report-models/cancel-report.model';


@Component({
  selector: 'app-cancel-report',
  templateUrl: './cancel-report.component.html',
  styleUrls: ['./cancel-report.component.css']
})
export class CancelReportComponent {

  // data preambles
  userDetails: UserData = new UserData('', '');
  currTrip: Trip = new Trip();
  cancelReport: CancelReport = new CancelReport();

  constructor(
    public auth: AuthService,
    private router: Router,
    private toastr: ToastrService,
    private onlineStatusService: OnlineStatusService,
    private localStorageService: LocalStorageService,
  ) {

    if (this.onlineStatusService.getStatus() === OnlineStatusType.ONLINE) {
      this.auth.isAuthenticated$.subscribe((authd: boolean) => {
        if (authd) {
          this.auth.user$.subscribe((user: User | null | undefined) => {
            if (user) {
              this.userDetails = this.localStorageService.getUser(user);
              this.initialize();
              return;
            }
            else {
              this.toastr.error("Please authenticate again", "Cannot get user data");
              this.localStorageService.logout();
              return;
            }
          });
        }
        else {
          this.toastr.error("Please authenticate again", "Not authenticated");
          this.localStorageService.logout();
          return;
        }
      });
    } else { // user is offline
      let currID: string = this.localStorageService.getCurrID();
      this.userDetails = this.localStorageService.getUserByID(currID);
      this.initialize();

    }

  }

  initialize(): void {
    this.currTrip = this.localStorageService.validateCurrTrip(this.userDetails.currTrip);
  }

  // go back to the active trip home screen
  toHome(): void {
    this.router.navigate(['active-trip-home']);
  }

  // send cancel report to DFO
  sendToDFO(): void {
    this.saveReportData(); // save data to local storage
    this.updateHomepage();
    this.router.navigate(['submitted-report'], { state: { name: 'Cancel Fishing Report' } });
  }

  saveReportData(): void {

    this.cancelReport.submissionDate = new Date(); // get current date/time
    this.cancelReport.isComplete = true; // set submission flag
    this.currTrip.cancelFishingReport = this.cancelReport; // copy over the report

    console.log(this.currTrip.cancelFishingReport);
    // save currTrip to local storage
    this.userDetails.currTrip = this.currTrip;
    this.localStorageService.saveData(this.userDetails.userID, this.userDetails); 

  }

  updateHomepage(): void {
    this.userDetails.currHomepage = 'no-trip-home';
    this.localStorageService.saveData(this.userDetails.userID, this.userDetails);
  }

}
