import { VesselData } from './vessel-data.model';
import { Trip } from '../trip-models/trip.model';
import { Net } from '../trip-models/gear-models/net.model';

export class UserData {

  // Auth0 ID
  userID: string = '';

  // Email for signup
  userEmail: string = '';

  // current homepage
  currHomepage: string = '';

  // saved user trips
  prevTrips: Trip[] = [];

  // saved user vessels
  vessels: VesselData[] = [];

  // current trip 
  currTrip: Trip = new Trip();

  // saved nets
  nets: Net[] = [];

  constructor(id: string, email: string) {
    this.userID = id;
    this.userEmail = email;
  }

  public toString(): string {
    return this.userEmail + ', ' + this.userID;
  }

  public isValid(): boolean {
    return !!this.userID && !!this.userEmail;
  }

}
