import { Injectable, Inject } from '@angular/core';
import { AuthService } from '@auth0/auth0-angular';
import { Router } from '@angular/router';
import { ToastrService } from 'ngx-toastr';
import { User } from '@auth0/auth0-angular';
import { UserData } from '../data-models/user-models/user-data.model';
import { VesselData } from '../data-models/user-models/vessel-data.model';
import { LicenseArea } from '../data-models/trip-models/license-area.model';
import { SpeciesData } from '../data-models/catch-table-models/species-data.model';
import { Gear } from '../data-models/trip-models/gear-models/gear.model';
import { Trip } from '../data-models/trip-models/trip.model';
import { Tel } from '../tel-input/tel-input.component';
import { Net } from '../data-models/trip-models/gear-models/net.model';
import { TableRules } from '../data-models/catch-table-models/table-rules.model';
import { StartReport } from '../data-models/report-models/start-report.model';
import { OnlineStatusService, OnlineStatusType } from 'ngx-online-status';
import { DOCUMENT } from '@angular/common';

@Injectable({
  providedIn: 'root'
})
export class LocalStorageService {

  constructor(
    public auth: AuthService,
    private router: Router,
    private toastr: ToastrService,
    private onlineStatusService: OnlineStatusService,
    @Inject(DOCUMENT) public document: Document
  ) {

    // initialize default lists
    this.setLists();

  }

  // save data to local storage through [key, value] pair
  public saveData(key: string, value: any): void {
    localStorage.setItem(key, JSON.stringify(value));
  }

  // get data from key
  public getData(key: string): any {
    let item: string | null = localStorage.getItem(key);
    if (item) {
      return JSON.parse(item);
    }
    return null;
  }

  // remove data given key
  public removeData(key: string): void {
    localStorage.removeItem(key);
  }

  // new user after sign up
  public createNewUser(user: User | null | undefined): UserData {

    let id: string | undefined = user?.sub;
    let email: string | undefined = user?.email;

    if (!id || !email) {
      return new UserData('', '');
    }

    let newUser: UserData = new UserData(id, email);
    newUser.vessels = this.setVessels();
    newUser.currTrip = this.setCurrTrip();
    newUser.nets = this.setNets();
    newUser.currHomepage = this.setHomepage();

    this.saveData(id, newUser);
    let userIDs: string[] = this.getAllUserIDs();
    userIDs.push(id);
    this.saveData('userIDs', userIDs);

    return newUser;

  }

  // get the current user ID for offline data retrieval
  public getCurrID(): string {
    return this.getData('currID');
  }

  // get all the user ids in local storage
  public getAllUserIDs(): string[] {
    return this.getStringList('userIDs');
  }

  // check if homepage is defined
  private validateHomepage(homepage: string | null): string {
    if (!homepage) {
      return this.setHomepage();
    }
    return homepage;
  }

  // check if user vessels are defined
  private validateVessels(vessels: any): VesselData[] {

    // if vessels do not exist, set the default ones
    if (!vessels) {
      return this.setVessels();
    }

    let userVessels: VesselData[] = [];
    for (let vessel of vessels) {
      userVessels.push(new VesselData(vessel.vesselName, vessel.vesselCoastNumber, vessel.isChecked));
    }
    return userVessels;
  }

  private validateStartFishingReport(startReport: any): StartReport {
    if(!startReport){
      return this.setStartReport();
    }
    return startReport;
  }

  public validatePrevTrips(prevTrips: any): Trip[] {

    if(!prevTrips){
      return this.setPrevTrips();
    }

    let prevTripList: Trip[] = [];
    for (let trip of prevTrips) {
      let userPrevTrip: Trip = new Trip();
      userPrevTrip.tripID = trip.tripID;
      userPrevTrip.gear = trip.gear;
      userPrevTrip.vessel = trip.vessel;
      userPrevTrip.licenseArea = trip.licenseArea;
      userPrevTrip.master = trip.master;
      userPrevTrip.fin = trip.fin;
      userPrevTrip.contactName = trip.contactName;
      userPrevTrip.phoneNum = trip.phoneNum;
      userPrevTrip.offloadNeeded = trip.offloadNeeded;
      userPrevTrip.isSaved = trip.isSaved;
      userPrevTrip.startFishingReport = trip.startFishingReport;
      userPrevTrip.endFishingReport = trip.endFishingReport;
      userPrevTrip.cancelFishingReport = trip.cancelFishingReport;
      userPrevTrip.pauseFishingReports = trip.pauseFishingReports;
      userPrevTrip.dailyCatchReports = trip.dailyCatchReports;
      userPrevTrip.offloadReports = trip.offloadReports;
      prevTripList.push(userPrevTrip);
    }

    return prevTrips;
  }

  // check if user fishing report is defined
  public validateCurrTrip(currTrip: any): Trip {

    if (!currTrip) {
      return this.setCurrTrip();
    }

    let usercurrTrip: Trip = new Trip(); 

    usercurrTrip.tripID = currTrip.tripID;
    usercurrTrip.master = currTrip.master;
    usercurrTrip.fin = currTrip.fin;
    usercurrTrip.contactName = currTrip.contactName;
    usercurrTrip.phoneNum = this.getPhoneNum(currTrip.phoneNum);
    let userGear: any = currTrip.gear;
    let userVessel: any = currTrip.vessel;
    let userLicenseArea: any = currTrip.licenseArea;
    usercurrTrip.gear = new Gear(userGear.name, userGear.logbookID, userGear.isChecked);
    usercurrTrip.vessel = new VesselData(userVessel.vesselName, userVessel.vesselCoastNumber, userVessel.isChecked);
    usercurrTrip.licenseArea = new LicenseArea(userLicenseArea.licenseName, userLicenseArea.licenseLocation, userLicenseArea.isChecked);
    usercurrTrip.offloadNeeded = currTrip.offloadNeeded; 
    usercurrTrip.isSaved = currTrip.isSaved;
    usercurrTrip.startFishingReport = currTrip.startFishingReport;
    usercurrTrip.endFishingReport = currTrip.endFishingReport;
    usercurrTrip.pauseFishingReports = currTrip.pauseFishingReports;
    usercurrTrip.cancelFishingReport = currTrip.cancelFishingReport;
    usercurrTrip.dailyCatchReports = currTrip.dailyCatchReports;
    usercurrTrip.offloadReports = currTrip.offloadReports;
    return usercurrTrip;

  }

  // check if user nets are defined
  private validateNets(nets: any): Net[] {

    // if nets do not exist, set the default ones
    if (!nets) {
      return this.setNets();
    }

    let userNets: Net[] = [];
    for (let net of nets) {
      userNets.push(new Net(net.netType, net.netName, net.numStrands, net.length, net.weedlineDepth,
        net.weedlineDepthUnits, net.hangRatio, net.meshSize, net.meshSizeUnits,
        net.numMeshes, net.isChecked));
    }
    return userNets;
  }


  // get current user data in local storage
  public getUserByID(id: string): UserData {

    let user: UserData = new UserData('', '');
    let userString: any = this.getData(id);

    if (userString) {
      user.userID = userString.userID;
      user.userEmail = userString.userEmail;
      user.vessels = this.validateVessels(userString.vessels);
      user.currTrip = this.validateCurrTrip(userString.currTrip);
      user.currTrip.startFishingReport = this.validateStartFishingReport(userString.currTrip.startFishingReport);
      user.prevTrips = this.validatePrevTrips(userString.prevTrips);
      user.nets = this.validateNets(userString.nets);
      user.currHomepage = this.validateHomepage(userString.currHomepage);
      this.saveData(user.userID, user);
    }
    else {
      this.toastr.error("Cannot get user data", "Data error");
    }
    
    return user;

  }

  // get current user in local storage if online (will use database to sync storage once implemented)
  public getUser(user: User): UserData {

    let thisUser: UserData = new UserData('', '');
    let id: string | undefined = user.sub;

    // if ID does not exist, return an empty user
    if (!id) {
      return thisUser;
    }

    let userString: any = this.getData(id);

    if (userString) {
      thisUser.userID = userString.userID;
      thisUser.userEmail = userString.userEmail;
      thisUser.vessels = this.validateVessels(userString.vessels);
      thisUser.currTrip = this.validateCurrTrip(userString.currTrip);
      thisUser.currTrip.startFishingReport = this.validateStartFishingReport(userString.currTrip.startFishingReport);
      thisUser.prevTrips = this.validatePrevTrips(userString.prevTrips);
      thisUser.nets = this.validateNets(userString.nets);
      thisUser.currHomepage = this.validateHomepage(userString.currHomepage);
      this.saveData(id, thisUser);
    }
    else {
      this.toastr.error("Try logging in again", "Data error");
    }

    return thisUser;

  }


  // get license areas stored in local storage
  public getLicenseAreas(): LicenseArea[] {

    let licenseAreas: any = this.getData('licenseAreaList');

    // if license areas do not exist, set them again
    if (!licenseAreas) {
      this.setLicenseAreas();
      licenseAreas = this.getData('licenseAreaList');
    }

    let licenseAreaArray: LicenseArea[] = [];
    for (let licenseArea of licenseAreas) {
      licenseAreaArray.push(new LicenseArea(licenseArea.licenseName, licenseArea.licenseLocation, licenseArea.isChecked));
    }

    return licenseAreaArray;

  }

  // get species stored in local storage
  public getSpeciesList(name: string): SpeciesData[] {

    let speciesList: any = this.getData(name);

    // if species list does not exist, set it again
    if (!speciesList) {
      this.setSpeciesList();
      speciesList = this.getData(name);
    }

    let speciesArray: SpeciesData[] = [];
    for (let species of speciesList) {
      speciesArray.push(new SpeciesData(species.speciesName));
    }

    return speciesArray;

  }

  // get gears stored in local storage
  public getGears(): Gear[] {
    let gearList: any = this.getData('gearList');

    // if gear list does not exist, set it again
    if (!gearList) {
      this.setGears();
      gearList = this.getData('gearList');
    }

    let gearArray: Gear[] = [];
    for (let gear of gearList) {
      gearArray.push(new Gear(gear.name, gear.logbookID, gear.isChecked));
    }

    return gearArray;

  }

  // get table rules for catch table data in local storage
  public getTableRules(gearName: string): TableRules {

    let newTable: TableRules = new TableRules();
    let tableList: any = this.getData('tableRulesList');

    // if table list does not exist, set it again
    if (!tableList) {
      this.setTableRules();
      tableList = this.getData('tableRulesList');
    }

    for(let table of tableList) {
        if(table.gearName === gearName){
          newTable = table;
        }
    }

    return newTable;

  }


  // get phone number from local storage
  private getPhoneNum(phoneNum: any): Tel {
    return new Tel(phoneNum.area, phoneNum.exchange, phoneNum.subscriber);
  }

  // get list of strings stored in local storage
  public getStringList(name: string): string[] {

    let stringList: any = this.getData(name);

    // if string list does not exist, set it again
    if (!stringList) {
      this.saveData(name, []);
      return this.getData(name);
    }

    let stringArray: string[] = [];
    for (let currString of stringList) {
      stringArray.push(currString.toString());
    }

    return stringArray;

  }

  // set the default list of vessels
  private setVessels(): VesselData[] {
    let vessels: VesselData[] = [
      new VesselData("My Vessel 1", "872357", true),
      new VesselData("My Vessel 2", "584317"),
      new VesselData("My Vessel 3", "872358"),
      new VesselData("My Vessel 4", "584318"),
    ];
    return vessels;
  }

  private setPrevTrips(): Trip[] {
    let prevTrips: Trip[] = [];
    return prevTrips;
  }

  // set the no active trip homepage to a new user
  private setHomepage(): string {
    return 'no-trip-home';
  }

  // set a start fishing report to a new user 
  private setStartReport(): StartReport {
    let newStartReport: StartReport = new StartReport();
    return newStartReport;
  }

  // set the default data for the current trip
  private setCurrTrip(): Trip {
    return new Trip();
  }

  //set the default list of nets (remove later)
  private setNets(): Net[] {
    let nets: Net[] = [
      new Net("Alaska Twist", "My Net 1", "10", "10", "cm", "2", "15", "cm", "30", "50", true),
      new Net("Multi Strand", "My Net 2", "3", "60", "in", "7", "10", "in", "20"),
      new Net("Combination", "My Net 3", "8", "60", "mm", "7", "8", "mm", "100")
    ];
    return nets;
  }

  // set gears to local storage (remove later)
  private setGears(): void {

    let gearList: Gear[] = [
      new Gear('Gill Net', '1111', true),
      new Gear('Seine', '2222'),
      new Gear('Troll', '3333')
    ];
    
    this.saveData('gearList', gearList);
  }

  // set table rules for what species should show up in the catch table for different gear types
  private setTableRules(): void {

    let gillNetTable = new TableRules('daily-catch', 'Gill Net');
    gillNetTable.containsSpecies = ['Sockeye', 'Coho', 'Pink', 'Chum', 'Chinook', 'Steelhead', 'Atlantic', 'Dogfish', 'Green Sturgeon', 'White Sturgeon'];
    gillNetTable.excludesSpecies = ['Legal Sized Chinook', 'Sublegal Sized Chinook'];
    gillNetTable.releaseOnlySpecies =['Steelhead', 'White Sturgeon', 'Green Sturgeon'];

    let seineTable = new TableRules('daily-catch', 'Seine');
    seineTable.containsSpecies = ['Sockeye', 'Coho', 'Pink', 'Chum', 'Steelhead', 'Atlantic', 'Jack Chinook', 'Adult Chinook'];
    seineTable.excludesSpecies = ['Legal Sized Chinook', 'Sublegal Sized Chinook'];

    let trollTable = new TableRules('daily-catch', 'Troll');
    trollTable.containsSpecies = ['Sockeye', 'Coho', 'Pink', 'Chum', 'Atlantic', 'Grilse', 'Legal Sized Chinook', 'Sublegal Sized Chinook'];
    trollTable.excludesSpecies = ['Jack Chinook'];
    trollTable.releaseOnlySpecies = ['Sublegal Sized Chinook', 'Grilse'];

    let tableRulesList: TableRules[] = [gillNetTable, seineTable, trollTable];

    this.saveData('tableRulesList', tableRulesList);

  }

  // set license areas to local storage
  private setLicenseAreas(): void {
    let licenseAreaList: LicenseArea[] = [
      new LicenseArea("A", "Seine North", true),
      new LicenseArea("B", "Seine South"),
      new LicenseArea("C", "Gillnet North"),
      new LicenseArea("D", "Gillnet Vancouver Island"),
      new LicenseArea("E", "Gillnet Fraser + VI"),
      new LicenseArea("F", "Troll North"),
      new LicenseArea("G", "Troll WCVI"),
      new LicenseArea("H", "Troll Fraser")
    ];
    this.saveData('licenseAreaList', licenseAreaList);
  }

  // set fish to local storage
  // Note: we will eventually get this list from the DFO database, then will have to copy the names over into SpeciesData
  private setSpeciesList(): void {
    let fishList: SpeciesData[] = [
      new SpeciesData("Sockeye"),
      new SpeciesData("Coho"),
      new SpeciesData("Pink"),
      new SpeciesData("Chum"),
      new SpeciesData('Chinook'),
      new SpeciesData('Steelhead'),
      new SpeciesData('Atlantic'),
      new SpeciesData('Dogfish'),
      new SpeciesData('Green Sturgeon'),
      new SpeciesData('Grilse'), 
      new SpeciesData('Jack Chinook'),
      new SpeciesData('Adult Chinook'),
      new SpeciesData('Legal Sized Chinook'),
      new SpeciesData('Sublegal Sized Chinook'),
      new SpeciesData('White Sturgeon'),
      new SpeciesData('Mackerel'),
      new SpeciesData('Lingcod'),
      new SpeciesData('Halibut'),
      new SpeciesData('Aurora Rockfish'),
      new SpeciesData('Canary Rockfish'),
      new SpeciesData('China Rockfish'),
      new SpeciesData('Tiger Rockfish'),
      new SpeciesData('Redbanded Rockfish'),
      new SpeciesData('Unknown Rockfish')
    ];
    this.saveData('fishList', fishList);
  }

  // set nets to local storage
  private setNetTypes(): void {
    let netTypes: string[] = [
      'Alaska Twist',
      'Multi Strand',
      'Combination'
    ];
    this.saveData('netTypes', netTypes);
  }

  // data that all users receive, put into local storage
  private setLists(): void {
    this.setTableRules();
    this.setGears();
    this.setLicenseAreas();
    this.setSpeciesList();
    this.setNetTypes();
  }

  // log out of account and go back to login screen
  logout(): void {
    this.removeData('currID');
    if (this.onlineStatusService.getStatus() === OnlineStatusType.OFFLINE) {
      this.router.navigate(['login']);
      return;
    }
    this.auth.logout({
      logoutParams: {
        returnTo: this.document.location.origin
      }
    });
  }

}
